import React, { Component } from 'react';
import LanguageSelect from "./language_select";

class TopBar extends Component {
	state = {
		isActive: true,
		isLangActive: true
	};

	ToggleClass = () => {
		this.setState({ isActive: !this.state.isActive })
	};

	ToggleLang = () => {
		this.setState({ isLangActive: !this.state.isLangActive })
	};

	render() {
		return(
			<div className={`top-bar container ${(this.state.isActive) ? 'menu-closed' : 'menu-open'}`}>
				<a href="#" className="menu-toggle" onClick={this.ToggleClass} >
					<div className="line" />
				</a>
				<div className="logo" />
				<div className="main-nav">
					<div className="menu">
						<a href="#" className="menu-link">About Us</a>
						<a href="#" className="menu-link">Services</a>
						<a href="#" className="menu-link">Projects</a>
						<a href="#" className="menu-link">FAQ</a>
						<a href="#" className="menu-link">Contact Us</a>
					</div>
					<a href="http://delta.at" target="_blank" className="button">Back to Delta.at</a>
					<div className="language">
						<div className={`languages ${(this.state.isLangActive) ? 'lang-close' : 'lang-open'}`} onClick={this.ToggleLang}>
							<div className="lang-list">
								<a href="#"><img src={require(`../../images/flags/ua.gif`)} /> Українська</a>
							</div>
							<img src={require(`../../images/flags/gb.gif`)} /> English
						</div>
					</div>
				</div>
			</div>
		)
	}
}

export default TopBar;


